/* App.css */

body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f7f7f7;
}

.App {
  max-width: default;
  margin: 0 auto;
  padding: 20px;
}

header {
  background-color: #333;
  color: #fff;
  padding: 10px 0;
  text-align: center;
}

.app-logo {
  width: 80px;
  height: auto;
  margin-right: 10px;
  vertical-align: middle;
}

.app-name {
  display: inline-block;
  margin: 0;
}

.main-content {
  text-align: center;
  margin-top: 100px; /* Adjust this value as needed */
}

.auth-buttons {
  margin-top: 20px;
}

.auth-buttons button {
  margin: 0 10px;
  padding: 10px 20px;
  border: none;
  text-align: center;
  border-radius: 5px;
  background-color: #4CAF50;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
}

.auth-buttons button:hover {
  background-color: #45a049;
}

/* Centering content */
header,
.main-content,
.auth-buttons {
  text-align: center;
}

/* Resetting margin and padding for ul */
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

/* Styling navigation links */
nav ul li {
  display: inline-block;
  margin-right: 20px;
}

nav ul li a {
  color: #fff;
  text-decoration: none;
  font-size: 18px;
}

nav ul li a:hover {
  color: #ffd700; /* Change color on hover */
}

/* Styling logout button */
.logout button {
  background-color: #dc3545; /* Red color for logout button */
}

.logout button:hover {
  background-color: #c82333; /* Darker red color on hover */
}
